.collapse {
	&_bar {
		border-radius: 0.3rem;
		height: 2rem;
		display: flex;
		align-items: center;
		justify-content: space-between;
		@extend %style;
	}
	&_title {
		padding: 1rem;
	}
	svg {
		margin-right: 1rem;
	}
	&_description {
		@extend %primary-color;
		padding: 1rem;
	}
	&--logement {
		width: 48%;
	}
	&--apropos {
		margin-top: 2rem;
	}
}

.Apropos_content_collapse {
	margin: 0 7rem 4rem 7rem;
}

.logement_collapse {
	&_container {
		margin-top: 1.5rem;
		margin-bottom: 1.5rem;
		display: flex;
		justify-content: space-between;
	}
}

@media only screen and (max-width: $mobile) {
	.Apropos_content_collapse {
		margin: 0 0rem 2rem 0rem;
	}
	.logement_collapse_container {
		flex-direction: column;
	}
	.collapse--logement {
		width: 100%;
		margin-bottom: 1.5rem;
	}
}
